/* Product Detail Page */
.product-detail-page {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.product-detail-container {
  display: flex;
  gap: 40px;
  align-items: flex-start;
}

.product-detail-container .product-image {
  flex: 1;
  max-width: 400px;
}

.product-detail-container .product-image img {
  width: 100%;
  height: auto;
  border-radius: 8px;
}

.product-info {
  flex: 1;
}

.product-info h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.product-info .category {
  color: #666;
  margin-bottom: 20px;
}

.product-info .price {
  font-size: 1.5rem;
  font-weight: bold;
  color: #e91e63;
  margin-bottom: 20px;
}

.product-info .description {
  margin-bottom: 30px;
  line-height: 1.6;
}

.product-actions {
  display: flex;
  gap: 15px;
}

.product-actions .like-btn,
.product-actions .add-to-cart-btn {
  padding: 10px 20px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
}

.product-actions .like-btn {
  background-color: #f5f5f5;
}

.product-actions .add-to-cart-btn {
  background-color: #e91e63;
  color: white;
}

/* Responsive Design for Product Detail */
@media (max-width: 768px) {
  .product-detail-container {
    flex-direction: column;
    gap: 20px;
  }

  .product-info h1 {
    font-size: 2rem;
  }

  .product-actions {
    flex-direction: column;
  }
}
